import SmartUdyog from '../../assets/png/dark-logo.png'
import Dhoot from '../../assets/png/dhoot.png'

const drawerWidth = parseInt(localStorage.getItem('draweWidth'));

const Drawer = styled(MuiDrawer, {
  shouldForwardProp: (prop) => prop !== "open",
})(({ open }) => ({
  width: drawerWidth,
  flexShrink: 0,
  whiteSpace: "nowrap",
  boxSizing: "border-box",
  ...(open && {
    width: drawerWidth,
    // marginLeft:10,
    overflowX: "hidden",
    "& .MuiDrawer-paper": {
      width: drawerWidth,
      overflowX: "hidden",
    },
  }),
  ...(!open && {
    overflowX: "hidden",
    width: "50px",
    "& .MuiDrawer-paper": {
      overflowX: "hidden",
      width: "50px",
    },
  }),
}));

const usr = {
  title: "Users",
  icon: PeopleOutline,
  path: "/users",
}

const ten =  {
  title: "Tenants",
  icon: InventoryIcon,
  path: "/tenants",
}
const machine = {
  title: "Machine Master",
  icon: PrecisionManufacturingIcon,
  path: "/companypage",
}
const downtime = {
  title: "Downtime Master",
  icon: HourglassBottomIcon,
  path: "/downtime-master",
}
const storeMaster= {
  title: "Store Master",
  icon: Construction,
  path: "/store-master",
}

const modelMaster ={
  title:"Model Master",
  icon:ModelTrainingIcon,
  path:"/model-master"
}

const partsRejection ={
  title:"Parts Rejection",
  icon: SwipeLeftIcon,
  path:"/parts-rejection"
}
const shiftMaster ={
  title:"Shift Master",
  icon: CalendarMonthIcon,
  path:"/shift-master"
}

const notification ={
  title:"Notification",
  icon:NotificationsActiveIcon,
  path:"/notification"
}

const consumable ={
  title:"Consumable",
  icon:StoreIcon,
  path:'/consumable'
}

const profile = {
  title:"Profile",
  icon:PersonIcon,
  path:"/profile"
}
const RunningOrder={
  title:"Order",
  icon:PersonIcon,
  path:'/running-order'
}
const Plan = {
  title: "Plan",
  icon: ShoppingCartOutlined,
  path: "/plan",
}

const LiveView = {
  title: "Live View",
  icon: Construction,
  path: "/live-view",
}
const MachineView = {
  title: "Machine Monitoring",
  icon: Construction,
  path: "/machine-view",
}
const Group =  {
  title: "Groups",
  icon: Construction,
  path: "/group",
}

const menu = [
{
  title: "Skill Matrix",
  icon: Widgets,
  path: "/skill-matrix",
},
{
  title: "Plan Training",
  icon: Widgets,
  path: "/plan-training",
},
{
  title: "Skill Page",
  icon: Widgets,
  path: "/skill-page",
},
{
  title: "Performance",
  icon: Widgets,
  path: "/report",
},
{
  title: "Test Portal",
  icon: Widgets,
  path: "/test-portal",
},
{
  title: "Question Bank",
  icon: Widgets,
  path: "/question-bank",
},
{
  title: "Test Master",
  icon: Widgets,
  path: "/test-upload",
},
{
  title: "Inbound List",
  icon: Widgets,
  path: "/store-inbound",
},
{
  title: "Outbound",
  icon: Widgets,
  path: "/store-outbound",
},
{
  title: "Outbound History",
  icon: TimelineIcon,
  path: "/store-outbound-history",
},
// {
//   title: "Settings",
//   icon: Settings,
//   path: "/settings",
// },
// {
//   title: "Store Order",
//   icon: ShoppingCartOutlined,
//   path: "/order",
// }
]
const NavigationLayout = () => {
  const [menu_items,setMenu_items] = useState([])
  const isLoggedIn = useSelector((state) => state[REDUCER_TYPES.AUTH]);
  const user = useSelector((state) => state[REDUCER_TYPES.AUTH].user?.data)
  const {logout,profile2} = useAuth()
  const [open, setOpen] = useState(true);
  const navigate = useNavigate();
  const { pathname } = useLocation();
  const toggle = () => setOpen((val) => !val);
  const [anchorEl, setAnchorEl] = useState(null);
  const openpop = Boolean(anchorEl);

  useEffect(()=>{ 
    // localStorage.setItem('draweWidth',270)
    localStorage.setItem('draweWidth',200)
    const sideMenu = []
    console.log("redux user",user)
    if(user?.role === ROLE.SUPERADMIN ||localStorage.getItem('role') === ROLE.SUPERADMIN){
      sideMenu.push(ten,...menu)
      setMenu_items(sideMenu)
    }else if(user?.role === ROLE.ADMINISTRATOR ||localStorage.getItem('role') === ROLE.ADMINISTRATOR){
      sideMenu.push(usr,storeMaster,machine,downtime,modelMaster,partsRejection,shiftMaster,notification,profile,RunningOrder,LiveView,MachineView,Group,...menu)
      setMenu_items(sideMenu)
    }else{
      setMenu_items(menu)
    }
    if(!localStorage.getItem('t_token')){
      console.log("islogin",user)
    // toast.warn("Please Login!")
      navigate('/login',{replace:true})
    }
  },[])

  const handleClick = (event) => {
    console.log("popup",event.currentTarget)
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  return (
   
    <>
    {/* {isLoggedIn &&( */}
    <AppBar variant="outlined" sx={{ zIndex: (theme) => theme.zIndex.drawer + 1, width: '100%', position: 'fixed',backgroundColor:COLORS.WHITE }}>
          <Stack sx={{flexDirection:'row',px:1,py:0.5,justifyContent:'space-between'}}>
            <Stack sx={{flexDirection:'row',gap:2,alignItems:'center'}}>
            <img src={SmartUdyog} width='80px' height='43px' style={{objectFit:'contain'}}/>
            <img src={Dhoot} width='100px' height='33px' style={{objectFit:'contain'}}/>
            </Stack>
            <Stack sx={{flexDirection:'row',gap:2,justifyContent:'flex-end',alignItems:'center'}}>
                <Typography className="fw-14" sx={{width:'100%',pr:2,mt:1,color:COLORS.TEXT_GRAY}}>
                Welcome {user?.name}
                </Typography>
                <Stack>
                <IconButton>
                  <NotificationsNoneOutlinedIcon fontSize="large"/>
                </IconButton>
                </Stack>
               
                <Toolbar
                sx={{ maxWidth: `calc(100vw - ${open ? 180 : 50}px)` }}
                disableGutters
                className="h-50 minh-50"
              >
                <Stack
                  direction="row"
                  alignItems="center"
                  justifyContent="space-between"
                  // className="w-full "
                >
                  {/* <Button
                    id="basic-button"
                    aria-controls={openpop ? 'basic-menu' : undefined}
                    aria-haspopup="true"
                    aria-expanded={openpop ? 'true' : undefined}
                    onClick={handleClick}
                  > */}
                  <IconButton 
                    id="basic-button"
                    aria-controls={openpop ? 'basic-menu' : undefined}
                    aria-haspopup="true"
                    aria-expanded={openpop ? 'true' : undefined}
                    onClick={handleClick}
                  >
                    <AccountCircleOutlinedIcon fontSize="large"/>
                  </IconButton>
                  {/* </Button> */}
                  <Menu             
                    id="basic-menu"
                    anchorEl={anchorEl}
                    open={openpop}
                    onClose={handleClose}
                    MenuListProps={{
                      'aria-labelledby': 'basic-button',
                    }}
                  >
                    {/* <MenuItem onClick={handleClose}>Profile</MenuItem>
                    <MenuItem onClick={handleClose}>My account</MenuItem> */}
                    <MenuItem sx={{py:0}} onClick={()=>{handleClose()}}>
                    <ListItemIcon>
                      <PersonIcon fontSize="small" />
                    </ListItemIcon>
                    <ListItemText>{user?.name}</ListItemText>
                    </MenuItem>
                    <Divider />
                    <MenuItem sx={{py:0}} onClick={()=>{handleClose()}}>
                    <ListItemIcon>
                      <EmailIcon fontSize="small" />
                    </ListItemIcon>
                    <ListItemText>{user?.email}</ListItemText>
                    </MenuItem>
                    <Divider />
                    <MenuItem sx={{py:0}} onClick={()=>{handleClose()}}>
                    <ListItemIcon>
                      <PersonIcon fontSize="small" />
                    </ListItemIcon>
                    <ListItemText>{user?.role}</ListItemText>
                    </MenuItem>
                    <Divider />
                    <MenuItem sx={{py:0}} onClick={()=>{handleClose();logout()}}>
                    <ListItemIcon>
                      <Logout fontSize="small" />
                    </ListItemIcon>
                    <ListItemText>Logout</ListItemText>
                    </MenuItem>
                    {/* <Divider /> */}
                  </Menu> 
                </Stack>
                </Toolbar>
            </Stack>          
          </Stack>
    </AppBar>
      <Drawer open={open} variant="permanent" onClose={toggle}  sx={{}}>
        {/* <Box sx={{ width: drawerWidth,mt:8,backgroundColor:COLORS.BG_GRAY}} role="presentation">
          <h3 className={`${!open ? "display-none" : "ml-2"}`}>TRACEABILITY</h3>
          <IconButton onClick={toggle} sx={{ml:0.5}}>
            {open ? <ChevronRight /> : <ChevronLeft />}
          </IconButton>
        </Box> */}
        <Box sx={{ width: drawerWidth ,backgroundColor:COLORS.BG_GRAY,mt:7}} role="presentation">
         <List dense>
            {menu_items.map(({ title, path, icon }) => (
              <ListItem
                key={path}
                sx={{ml:1}}
                // className={`${pathname === path && "bg-opaque"}`}
                button
                onClick={() => navigate(path)}
              >
                {/* <ListItemIcon className="minw-40">
                  <Icon
                    component={icon}
                    className={`${
                      pathname === path ? "fw-700" : "fw-700"
                    }`}
                  />
                </ListItemIcon> */}
                <ListItemText sx={{'& .MuiListItemText-primary': {
                  fontSize: '16px', // Change font size
                  fontWeight:`${pathname === path?'700':'500'}`},
                }}

                  primary={title}
                  className={`${pathname === path?"fw-700" : "fw-700"}`}
                />
              </ListItem>
            ))}
            {/* <ListItem>
              <ListItemIcon>
                <Home/>
                <ListItemText primary='submenu'/>
              </ListItemIcon>
            </ListItem> */}
          </List>     
        </Box>
      </Drawer>
        <Box
          sx={{
            mt:8,
            position: "relative",
            left: open ? drawerWidth : "50px",
            overflow: "hidden",
          }}
        >      
          <Box sx={{ maxWidth: `calc(100vw - ${open ? drawerWidth : 50}px)` }}>
            <Routes />
          </Box>
        </Box>
    
 
    {/* )} */}
    </>
          
  )
};

export default NavigationLayout;
